
#resumePreview {
  background: #fff;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.25);
  padding: 2em;
  color: #333;
  overflow: auto;
  line-height: 1.2;
}
* {
  box-sizing: border-box;
  font-variant: normal;
  font-weight: normal;
}
ol {
  list-style: none;
}
section + section {
  margin-top: 2em;
}
p {
  white-space: pre-line;
}
section > h2:first-child {
  background: #ddd;
  display: inline-block;
  padding: .2em;
  margin-bottom: .5em;
}
section[data-name="profile"] > h1 {
  margin: .3em 0;
  font-size: 3em;
}
section[data-name="workHistory"] li + li,
section[data-name="projects"] li + li,
section[data-name="awards"] li + li {
  margin-top: 1em;
}
section[data-name="workHistory"] li h3,
section[data-name="projects"] li h3,
section[data-name="awards"] li h3 {
  border-bottom: 1px solid #999;
  padding-bottom: .3em;
  margin-bottom: .3em;
}
section[data-name="education"] li {
  line-height: 1.5;
}
section[data-name="contacts"] td:first-child {
  padding-right: 1em;
}



